.item__link::before {
  display: inline-block;
  background-image: url("../img/sprite.svg");
  background-size: 90px 56px;
}

.item {
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
  justify-content: flex-start;
  width: 380px;
  height: 525px;
  background: #ffffff;
  box-shadow: 5px 20px 50px rgba(16, 112, 177, 0.2);
  border-radius: 10px;
  margin: 0 0 100px 0;
  padding: 48px;
}

.item__icon {
  margin-bottom: 55px;
  align-self: center;
}

.item__icon_pos1 {
  margin-right: 58px;
}

.item__icon_pos2 {
  margin-top: -71px;
  margin-left: 8px;
  margin-bottom: 50px;
}

.item__icon_pos3 {
  margin-right: 40px;
}

.item__name {
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 16px;
  letter-spacing: 0.055em;
}

.item__short-info {
  opacity: 0.6;
  max-height: 284px;
  overflow: hidden;
  margin-bottom: 32px;
}

.item__link {
  font-size: 14px;
  line-height: 30px;
  border-color: transparent;
  position: relative;
  width: 124px;
}

.item__link::before {
  width: 17px;
  height: 17px;
  background-position: -69px -5px;
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate3d(0, -50%, 0);
}